body {
  max-width: 50%;
  margin: auto;
}
:root {
  font-size: 11px;
}
@media (min-width: 768px) {
  :root {
    font-size: 14px;
  }
}
@media (min-width: 992px) {
  :root {
    font-size: 20px;
  }
}
@media (min-width: 1200px) {
  :root {
    font-size: 22px;
  }
}
@media (min-width: 1400px) {
  :root {
    font-size: 24px;
  }
}
